.lightrope {
  text-align: center;
  white-space: nowrap;
  z-index: 1;
  pointer-events: none;
  width: 100%;
  margin: -15px 0 0;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.lightrope li {
  width: 12px;
  height: 20px;
  background: #ff0;
  border-radius: 50%;
  margin: 20px;
  padding: 0;
  list-style: none;
  animation-name: flash-1;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-fill-mode: both;
  display: inline-block;
  position: relative;
  box-shadow: 0 4.66667px 24px 3px #ff0;
}

.lightrope li:nth-child(2n+1) {
  background: #ff0;
  animation-name: flash-2;
  animation-duration: 1s;
  box-shadow: 0 4.66667px 24px 3px #fe0;
}

.lightrope li:nth-child(4n+2) {
  background: #ff0;
  animation-name: flash-3;
  animation-duration: 1.1s;
  box-shadow: 0 4.66667px 24px 3px #ff0;
}

.lightrope li:nth-child(2n+1) {
  animation-duration: 1.8s;
}

.lightrope li:nth-child(3n+1) {
  animation-duration: 1.4s;
}

.lightrope li:before {
  content: "";
  width: 10px;
  height: 9.33333px;
  background: #222;
  border-radius: 3px;
  position: absolute;
  top: -4.66667px;
  left: 1px;
}

.lightrope li:after {
  content: "";
  width: 52px;
  height: 18.6667px;
  border-bottom: 2px solid #222;
  border-radius: 50%;
  position: absolute;
  top: -14px;
  left: 9px;
}

.lightrope li:last-child:after {
  content: none;
}

.lightrope li:first-child {
  margin-left: -40px;
}

@-webkit-keyframes flash-1 {
  0%, 100% {
    background: #ff0;
    box-shadow: 0 4.66667px 24px 3px #ff0;
  }

  50% {
    background: #00c6f7;
    box-shadow: 0 4.66667px 24px 3px #00bdf7;
  }
}

@keyframes flash-1 {
  0%, 100% {
    background: #ff0;
    box-shadow: 0 4.66667px 24px 3px #ff0;
  }

  50% {
    background: rgba(0, 231, 247, .992);
    box-shadow: 0 4.66667px 24px 3px #00f7e3;
  }
}

@-webkit-keyframes flash-2 {
  0%, 100% {
    background: #ff0;
    box-shadow: 0 4.66667px 24px 3px #ff0;
  }

  50% {
    background: #0ff;
    box-shadow: 0 4.66667px 24px 3px rgba(0, 255, 255, .984);
  }
}

@keyframes flash-2 {
  0%, 100% {
    background: #ff0;
    box-shadow: 0 4.66667px 24px 3px #ff0;
  }

  50% {
    background: #06b0f3;
    box-shadow: 0 4.66667px 24px 3px rgba(6, 176, 243, .918);
  }
}

@-webkit-keyframes flash-3 {
  0%, 100% {
    background: #ff0;
    box-shadow: 0 4.66667px 24px 3px #ff0;
  }

  50% {
    background: #00e7f7;
    box-shadow: 0 4.66667px 24px 3px #03e9f1;
  }
}

@keyframes flash-3 {
  0%, 100% {
    background: #ff0;
    box-shadow: 0 4.66667px 24px 3px #ff0;
  }

  50% {
    background: #00daf7;
    box-shadow: 0 4.66667px 24px 3px #009cf7;
  }
}

