.preloader {
  width: 100%;
  height: 100%;
  z-index: 999;
  visibility: visible;
  opacity: 1;
  min-height: 100vh;
  background: linear-gradient(#a2000a, #cf0000);
  justify-content: center;
  align-items: center;
  transition: all 1.2s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
