  min-width: 1200px;
  opacity: 0;
  color: #000;
  background-color: #f7f7f7;
  border-radius: 8px;
  padding: 40px;
  transition: all .8s;
  position: relative;
  transform: perspective(600px)translate(0, -100%)rotateX(45deg);
}

