  .popup__close {
    cursor: default;
  }
}

.popup__close:before, .popup__close:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #000;
  transition: all .4s;
  position: absolute;
  right: 0;
}

.popup__close:before {
  top: calc(50% - 1px);
  transform: rotate(-45deg);
}

.popup__close:after {
  bottom: calc(50% - 1px);
  transform: rotate(45deg);
}

.popup__close span {
  top: calc(50% - 1px);
}

.popup__items {
  width: 100%;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 30px;
  display: grid;
}

